<template>
  <div class="PFRBox">
    <div class="title">
        项目角色列表-PFrole详情<el-button class="right" @click="Fnrole()" icon="el-icon-user">新建项目角色</el-button>
    </div>
    <el-divider></el-divider> 
    <!-- 角色列表 -->
    <template>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
            prop="date"
            label="角色"
            width="360">
            </el-table-column>
            <el-table-column
            label="操作">
                <template slot-scope="scope">
                    <el-button type="warning" plain>编辑</el-button>
                    <el-popconfirm
                        confirmButtonText='好的'
                        cancelButtonText='不用了'
                        icon="el-icon-info"
                        iconColor="red"
                        title="这是一段内容确定删除吗？">
                        <el-button type="danger" slot="reference" plain>删除</el-button>
                    </el-popconfirm>
                </template>
            </el-table-column>
        </el-table>
    </template>

  </div>
</template>

<script>
export default {
    data(){
        return{
             tableData:[//这是列表
                {
                    date: '2016-05-04'
                },
                {
                    date: '这是的歌'
                },
             ]
        };
    },
    methods:{
        Fnrole(){//点击去新建用户角色
            this.$router.push('/index/DFRdetails')
        }
    }
}
</script>

<style lang="stylus" scoped>
.PFRBox
    padding 30px
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
    .right
        float right
        margin-top -7px
</style>


